import React from 'react'
import { Link, NavLink } from 'react-router-dom'

export default function Navbar() {







function toggleLightMode()
{
  document.body.classList.toggle("colorred");
    // console.log("hi")
}










  
  return <>
  
  <nav className="navbar navbar-expand-lg bg-body-tertiary fixed-top">
  <div className="container-fluid">
    <Link className="navbar-brand" to="">Drinkly</Link>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <NavLink className="nav-link active" aria-current="page" to="/"  style={({ isActive }) => {
    return {
      fontWeight: isActive ? "bold " : "",
      color: isActive ? "white" : "",
      background: isActive ? "#16b693" : "",
      borderRadius: isActive ? "10px" : "",
      paddingLeft: isActive ? "5px" : "",
      fontSize: isActive ? "17px" : "18px",

    };
  }}>Home</NavLink>
        </li>
        <li className="nav-item">
          <NavLink className="nav-link" to="/categories"  style={({ isActive }) => {
    return {
      fontWeight: isActive ? "bold " : "",
      color: isActive ? "white" : "",
      background: isActive ? "#16b693" : "",
      borderRadius: isActive ? "10px" : "",
      paddingLeft: isActive ? "5px" : "",
      fontSize: isActive ? "17px" : "18px",

    };
  }}>Categories</NavLink>
        </li>
        <li className="nav-item">
          <NavLink className="nav-link" to="/ingredients" style={({ isActive }) => {
    return {
      fontWeight: isActive ? "bold " : "",
      color: isActive ? "white" : "",
      background: isActive ? "#16b693" : "",
      borderRadius: isActive ? "10px" : "",
      paddingLeft: isActive ? "5px" : "",
      fontSize: isActive ? "17px" : "18px",

    };
  }}>Ingredients</NavLink>
        </li>
        <li className="nav-item">
          <NavLink className="nav-link" to="/glassess" style={({ isActive }) => {
    return {
      fontWeight: isActive ? "bold " : "",
      color: isActive ? "white" : "",
      background: isActive ? "#16b693" : "",
      borderRadius: isActive ? "10px" : "",
      paddingLeft: isActive ? "5px" : "",
      fontSize: isActive ? "17px" : "18px",

    };
  }}>Glassess</NavLink>
        </li>
        <li className="nav-item dropdown">
          <a className="nav-link dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
          Alcoholic Filters
          </a>
          <ul className="dropdown-menu">

          <NavLink className="nav-link" to="/alcohil">Alcoholic</NavLink>
          <NavLink className="nav-link" to="/noalcohill">Non alcoholic</NavLink>
          <NavLink className="nav-link" to="/optionalcohil">Optional alcohol</NavLink>

          </ul>
        </li>

      </ul>



      <ul className="navbar-nav ms-auto mb-2 mb-lg-0">

      <li className="nav-item Searchitem">
          <NavLink className="nav-link" to="/searchbyname" style={({ isActive }) => {
    return {
      fontWeight: isActive ? "bold " : "",
      color: isActive ? "white" : "",
      background: isActive ? "#16b693" : "",
      borderRadius: isActive ? "10px" : "",
      paddingLeft: isActive ? "5px" : "",
      fontSize: isActive ? "17px" : "18px",

    };
  }}>Search For Drinks</NavLink>
        </li>

        <li className="nav-item Searchitem">
          <NavLink className="nav-link" to="/randomdrink" style={({ isActive }) => {
    return {
      fontWeight: isActive ? "bold " : "",
      color: isActive ? "white" : "",
      background: isActive ? "#16b693" : "",
      borderRadius: isActive ? "10px" : "",
      paddingLeft: isActive ? "5px" : "",
      fontSize: isActive ? "17px" : "18px",

    };
  }}>Random Drink</NavLink>
        </li>




<li className='mx-4 mt-1'>

{/* dark and lide mood */}

<label class="switch" >
  <span class="sun" onClick={toggleLightMode}>
    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><g fill="#ffd43b"><circle r="5" cy="12" cx="12"></circle><path d="m21 13h-1a1 1 0 0 1 0-2h1a1 1 0 0 1 0 2zm-17 0h-1a1 1 0 0 1 0-2h1a1 1 0 0 1 0 2zm13.66-5.66a1 1 0 0 1 -.66-.29 1 1 0 0 1 0-1.41l.71-.71a1 1 0 1 1 1.41 1.41l-.71.71a1 1 0 0 1 -.75.29zm-12.02 12.02a1 1 0 0 1 -.71-.29 1 1 0 0 1 0-1.41l.71-.66a1 1 0 0 1 1.41 1.41l-.71.71a1 1 0 0 1 -.7.24zm6.36-14.36a1 1 0 0 1 -1-1v-1a1 1 0 0 1 2 0v1a1 1 0 0 1 -1 1zm0 17a1 1 0 0 1 -1-1v-1a1 1 0 0 1 2 0v1a1 1 0 0 1 -1 1zm-5.66-14.66a1 1 0 0 1 -.7-.29l-.71-.71a1 1 0 0 1 1.41-1.41l.71.71a1 1 0 0 1 0 1.41 1 1 0 0 1 -.71.29zm12.02 12.02a1 1 0 0 1 -.7-.29l-.66-.71a1 1 0 0 1 1.36-1.36l.71.71a1 1 0 0 1 0 1.41 1 1 0 0 1 -.71.24z"></path></g></svg>
  </span>
  <span class="moon" onClick={toggleLightMode}><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 384 512"><path d="m223.5 32c-123.5 0-223.5 100.3-223.5 224s100 224 223.5 224c60.6 0 115.5-24.2 155.8-63.4 5-4.9 6.3-12.5 3.1-18.7s-10.1-9.7-17-8.5c-9.8 1.7-19.8 2.6-30.1 2.6-96.9 0-175.5-78.8-175.5-176 0-65.8 36-123.1 89.3-153.3 6.1-3.5 9.2-10.5 7.7-17.3s-7.3-11.9-14.3-12.5c-6.3-.5-12.6-.8-19-.8z"></path></svg></span>   
  <input type="checkbox" class="input"/>
  <span class="slider" onClick={toggleLightMode}></span>
</label>




</li>


        </ul>

    </div>
  </div>
</nav>
  
  </>
}
